<template>
    <div class="table-wrapper">
        <v-data-table
            :headers="tableData.headers"
            :items="tableData.data"
            hide-actions
            item-key="id"
        >
        <template v-slot:items="props">
            <tr :item-key="props.index">
                <td>{{ props.item.id }}</td>
                <td>{{ props.item.title }}</td>
                <td>{{ props.item.author }}</td>
                <td class="activeContent" @click="get_selection(props.index)">{{ props.item.content.split('.')[0] }}</td>
                <td class="actionIcons">
                    <span class="editRow" @click="rowClicked(props)"><i class="fa fa-pencil"></i></span>
                    <span class="deleteRow" @click="rowClicked(props, 'delete')"><i class="fa fa-trash"></i></span>
                </td>
            </tr>
        </template>
        </v-data-table>  
    </div>
</template>

<script>

export default {
    name: "tablecomp",
    props: {
        tableData: [Object, Array]
    },
    data(){
        return {
            showHighlightIcon : false
        }
    },
    methods: {
        rowClicked(rowData, action) {
           this.$emit('onRowClicked', rowData, action)
        },
        get_selection(rowIndex) {
            var inputText = document.getElementsByClassName("activeContent");
            this.$emit('getSelectedWord', rowIndex, inputText)
            // var selectedText = '';
            // if (window.getSelection) { 
            //     selectedText = window.getSelection().toString(); 
            // }
            // else if (document.getSelection) { 
            //     selectedText = document.getSelection(); 
            // } 
            // else if (document.selection) { 
            //     selectedText =  document.selection.createRange().text; 
            // } else return; 
            
            // if(selectedText !== '') {
            //     var myRegEx = /^([a-zA-Z0-9]+)$/;
            //     myRegEx.test(selectedText) ? this.highlight(rowIndex, selectedText) : selectedText = ""
            // }
        }
        // highlight(rowIndex, selectedText) {
        //     this.$store.commit("UPDATE_HIGHLIGHTED_WORDS", selectedText);
        //     var inputText = document.getElementsByClassName("activeContent");
        //     var innerHTML = inputText[rowIndex].innerHTML;
        //     var index = innerHTML.indexOf(selectedText);
        //     if (index >= 0) { 
        //     innerHTML = innerHTML.substring(0,index) + "<span class='highlight'>" + innerHTML.substring(index,index+selectedText.length) + "</span>" + innerHTML.substring(index + selectedText.length);
        //     inputText[rowIndex].innerHTML = innerHTML;
        //     }
        // }
    }
}
</script>


