import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations";
import actions from "./actions";

Vue.use(Vuex);

const store = new Vuex.Store({
    strict: false,
    state: {
        activeState: "manageBlogList",
        defaultBlogs:{
            "headers": [
                { "text": "ID","align": "left","sortable": false,"value": "id"},
                { "text": "Title","align": "left","sortable": false, "value": "title" },
                { "text": "Author","align": "left","sortable": false, "value": "author" },
                { "text": "Content","align": "left","sortable": false, "value": "content" },
                { "text": "Actions","align": "left","sortable": false, "value": "action" }
            ],
            "data": [
                {
                "id": "9781593275846",
                "title": "Eloquent JavaScript, Second Edition",
                "author": "Marijn Haverbeke",
                "content": "JavaScript +lies at the heart of almost every modern web application, from social apps to the newest browser-based games. Though simple for beginners to pick up and play with, JavaScript is a flexible, complex language that you can use to build full-scale applications."
                },
                {
                "id": "9781449331818",
                "title": "Learning JavaScript Design Patterns",
                "author": "Addy Osmani",
                "content": "With Learning JavaScript Design Patterns, you'll learn how to write beautiful, structured, and maintainable JavaScript by applying classical and modern design patterns to the language. If you want to keep your code efficient, more manageable, and up-to-date with the latest best practices, this book is for you."
                },
                {
                "id": "9781449365035",
                "title": "Speaking JavaScript",
                "author": "Axel Rauschmayer",
                "content": "Like it or not, JavaScript is everywhere these days-from browser to server to mobile-and now you, too, need to learn the language or dive deeper than you have. This concise book guides you into and through JavaScript, written by a veteran programmer who once found himself in the same position."
                },
                {
                "id": "9781491950296",
                "title": "Programming JavaScript Applications",
                "author": "Eric Elliott",
                "content": "Take advantage of JavaScript's power to build robust web-scale or enterprise applications that are easy to extend and maintain. By applying the design patterns outlined in this practical book, experienced JavaScript developers will learn how to write flexible and resilient code that's easier-yes, easier-to work with as your code base grows."
                },
                {
                "id": "9781593277574",
                "title": "Understanding ECMAScript 6",
                "author": "Nicholas C. Zakas",
                "content": "ECMAScript 6 represents the biggest update to the core of JavaScript in the history of the language. In Understanding ECMAScript 6, expert developer Nicholas C. Zakas provides a complete guide to the object types, syntax, and other exciting changes that ECMAScript 6 brings to JavaScript."
                },
                {
                "id": "9781491904244",
                "title": "You Don't Know JS",
                "author": "Kyle Simpson",
                "content": "No matter how much experience you have with JavaScript, odds are you don’t fully understand the language. As part of the \"You Don’t Know JS\" series, this compact guide focuses on new features available in ECMAScript 6 (ES6), the latest version of the standard upon which JavaScript is built."
                },
                {
                "id": "9781449325862",
                "title": "Git Pocket Guide",
                "author": "Richard E. Silverman",
                "content": "This pocket guide is the perfect on-the-job companion to Git, the distributed version control system. It provides a compact, readable introduction to Git for new users, as well as a reference to common commands and procedures for those of you with Git experience."
                },
                {
                "id": "9781449337711",
                "title": "Designing Evolvable Web APIs with ASP.NET",
                "author": "Glenn Block, et al.",
                "content": "Design and build Web APIs for a broad range of clients—including browsers and mobile devices—that can adapt to change over time. This practical, hands-on guide takes you through the theory and tools you need to build evolvable HTTP services with Microsoft’s ASP.NET Web API framework. In the process, you’ll learn how design and implement a real-world Web API."
                }
            ]
        },
        highlightedWords: [],
        previewContent:[]
    },
    actions,
    mutations
});

export default store;


