<template>
    <v-container fluid v-if="$store.state.activeState === 'highlightedList'">
        <v-layout row wrap>
            <v-flex xs6>
                <div class="highlight-wrapper">
                    <h3>List of Highlighted Words</h3>
                    <ul class="custom-ul" v-for="(item, key) in list" :key="key">
                        <li @click="viewBlogs(item)">{{item}}</li>
                    </ul>
                </div>
                <v-btn class="left cancelBtn highlight" @click="prevPage">cancel</v-btn>
            </v-flex>
            <v-flex xs6>
                <preview-data :prevData="prevData" @routeToBlog="routeToBlog"></preview-data>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
import previewData from './PreviewData';

export default {
    props: {
        list: [Array, Object],
        prevData: Array
    },
    components: {
        previewData
    },
    methods: {
        viewBlogs(item) {
            this.$emit('viewBlogs', item)
        },
        routeToBlog(item){
            this.$emit('routeToBlog', item)
        },
        prevPage() {
            this.$emit('prevPage')
        }
    }

}
</script>


